//Listeners
document.addEventListener('DOMContentLoaded', iniciarApp());

//Variables




//Funciones


function iniciarApp() {
    entrada();
    carruselPortafolio();
    mostrarMenuPrimario();
    mostrarMenuSecundario();
}
  //entrada

  function entrada() {
    const first = document.querySelector('.first');
    const second = document.querySelector('.second');
    const third = document.querySelector('.third');
  
    TweenMax.to(first, 1.5, {
        delay: .5,
        top: "-100%",
        ease: Expo.easeInOut
    });
    TweenMax.to(second, 1.5, {
        delay: .7,
        top: "-100%",
        ease: Expo.easeInOut
    });TweenMax.to(third, 1.5, {
        delay: .9,
        top: "-100%",
        ease: Expo.easeInOut
    });
  }

  //Menu Primario

  function mostrarMenuPrimario() {
    
    const flecha = document.getElementById('flecha--menu');

    flecha.addEventListener('click', ()=>{
      document.getElementById('menu--primario').classList.toggle('show');
    })
  }

  function mostrarMenuSecundario() {
    
    const menu= document.getElementById('menu--hamburguer');

    menu.addEventListener('click', ()=>{
      document.getElementById('menu--secundario').classList.toggle('show-2');
    })

  }



  //Carrusel

    function carruselPortafolio() {
        new Glider(document.querySelector('.proyectos--cards'), {
            slidesToShow: 1,
            dots: '#dots',
            draggable: true,
            responsive: [
                {
                  // screens greater than >= 775px
                  breakpoint: 775,
                  settings: {
                    // Set to `auto` and provide item width to adjust to viewport
                    slidesToShow: 2,
                    slidesToScroll: 4,
                    itemWidth: 150,
                    duration: 0.25
                  }
                },{
                  // screens greater than >= 1024px
                  breakpoint: 1024,
                  settings: {
                    slidesToShow: 2,
                    slidesToScroll: 4,
                    itemWidth: 150,
                    duration: 0.25
                  }
                }
              ]
        });
        
    }